import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { trigger, animate, style, group, animateChild, query, stagger, transition, state } from '@angular/animations';

import { EventService } from './list';

const entryInAnimation = trigger('entryInAnimation', [

  // route 'enter' transition
  transition(':enter', [

    // css styles at start of transition
    style({ opacity: 0 }),

    // animation and styles at end of transition
    animate('.3s', style({ opacity: 1 }))
  ])
]);

const routerTransition = trigger('routerTransition', [
  state('enter', style({ opacity: 1 })),
  state('leave', style({ opacity: 0 })),
  transition('enter => leave', animate('200ms ease')),
  transition('leave => enter', animate('200ms ease'))
]);



@Component({
  selector: 'app-blog',
  templateUrl: 'blog.html',
  animations: [entryInAnimation, routerTransition],
  styleUrls: [
    'blog.scss'
  ]
})
export class BlogComponent implements OnInit, OnDestroy {
  public state = 'enter';
  public sub: Subscription;
  private url: string;

  constructor(
    private router: Router,
    private event: EventService
  ) { }

  public ngOnInit() {
    this.state = 'enter';

    this.sub = this.event.getEmitter().subscribe(
      (v: string) => this.navigate(v)
    );
  }

  public ngOnDestroy() {
    this.sub.unsubscribe();
  }

  /**
   * Event called when the routerTransition event is done
   *
   * @param {*} $event the event information
   * @memberof BlogComponent
   */
  public animationDone($event) {
    if ($event['toState'] === 'leave') {
      this.router.navigateByUrl(this.url).then(
        (v) => {
          this.state = 'enter';
        }
      );
    }
  }

  /**
   * Function called from the list subscription service
   *
   * @param {string} url where to redirect the router outlet
   * @memberof BlogComponent
   */
  public navigate(url: string) {
    this.state = 'leave';
    this.url = url;
  }
}
