import {Component, OnInit} from '@angular/core';
import {environment} from '../../../environments/environment';

import { EventService } from '../list/list.service';

@Component({
  selector: 'app-blog-awoc',
  templateUrl: 'awoc.html',
  styleUrls: [
    'awoc.scss'
  ]
})
export class AwocComponent implements OnInit {

  constructor(
    private event: EventService
  ) {}

  public ngOnInit() {
  }

  public redirect(blogItem: string) {
    this.event.emit(`/blog${blogItem}`);
  }
}
