import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FlexLayoutModule } from '@angular/flex-layout';
import { RouterModule } from '@angular/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { BrowserModule } from '@angular/platform-browser';

import { ListComponent, EventService } from './list';
import { BlogComponent } from './blog.component';
import { AwocComponent } from './awoc';
import { WirelineComponent } from './wireline';
import { WebsiteComponent } from './website';

@NgModule({
  declarations: [
    ListComponent,
    BlogComponent,
    AwocComponent,
    WirelineComponent,
    WebsiteComponent
  ],
  providers: [
    EventService
  ],
  imports: [
    CommonModule,
    FlexLayoutModule,
    RouterModule.forChild([
      {path: '', component: BlogComponent, children: [
        { path: '', component: ListComponent },
        { path: 'awoc', component: AwocComponent },
        { path: 'wireline', component: WirelineComponent },
        { path: 'website', component: WebsiteComponent }
      ]}
    ])
  ]
})
export class BlogModule {}
